<template>
  <el-dialog title="编辑" :visible.sync="info.isShow">
    <el-form ref="formRef" :model="formData" label-width="80px">
      <el-form-item label="用户ID" prop="uid">
        <el-input
          placeholder="用户ID"
          v-model="formData.uid"
          :disabled="true"
        ></el-input>
      </el-form-item>
      <el-form-item label="昵称" prop="uid">
        <el-input placeholder="昵称" v-model="formData.nickname"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="phone">
        <el-input placeholder="手机号" v-model="formData.phone"></el-input>
      </el-form-item>
      <el-form-item label="注册日期" prop="addtime">
        <el-input
          placeholder="注册日期"
          v-model="formData.addtime"
          :disabled="true"
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="reset">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import vm from "../../utils/vm";
export default {
  props: ["info"],
  methods: {
    reset() {
      this.info.isShow = false;
    },
    submit() {
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$http.post("/memberedit", this.formData).then((res) => {
            if (res.code === 200) {
              this.$success(res.msg);
              this.$emit("updateList");
              this.reset();
            } else {
              this.$error(res.msg);
            }
          });
        }
      });
    },
  },
  created() {
    vm.$on("sendMemberData", (obj) => {
      this.info.isShow = true;
      this.formData = obj;
      let val = obj.addtime;
      const date = new Date(Number(val));
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      let h = date.getHours();
      let mm = date.getMinutes();
      let s = date.getSeconds();
      this.formData.addtime = `${y}-${m}-${d} ${h}:${mm}:${s}`;
    });
  },
  data() {
    return {
      formData: {
        uid: "",
        nickname: "",
        phone: "",
        addtime: "",
        password: "",
      },
    };
  },
};
</script>

<style>
</style>


