<template>
  <el-card>
    <el-table
      :data="memberList"
      style="width: 100%"
      @filter-change="filterChange"
    >
      <el-table-column align="center" prop="uid" label="用户ID" width="350">
      </el-table-column>
      <el-table-column align="center" prop="nickname" label="昵称" width="180">
      </el-table-column>
      <el-table-column align="center" prop="phone" label="手机号">
      </el-table-column>
      <el-table-column align="center" label="注册日期">
        <template v-slot="prpos">
          {{ prpos.row.addtime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column align="center" prop="status" label="操作">
        <template v-slot="props">
          <el-button
            @click="edit(props.row)"
            circle
            size="mini"
            type="success"
            icon="el-icon-edit"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <Modal :info="info" @updateList="updateList" />
  </el-card>
</template>

<script>
import vm from "../../utils/vm";
import { mapState, mapActions } from "vuex";
import Modal from "./Modal";
export default {
  data() {
    return {
      info: {
        isShow: false,
      },
    };
  },
  components: {
    Modal,
  },
  created() {
    this.getMemberListAction();
  },
  computed: {
    ...mapState({ memberList: (state) => state.member.memberList }),
  },
  methods: {
    ...mapActions("member", ["getMemberListAction"]),
    filterChange(filters) {
      console.log(filters);
    },
    edit(row) {
      vm.$emit("sendMemberData", { ...row });
    },
    updateList() {
      this.getMemberListAction();
    },
  },
};
</script>

<style>
</style>


